import React, {Component} from 'react';
import * as $ from 'jquery';
import LineChart from '../d3/lineChart/LineChart'
import Heatmap from '../d3/heatmap/Heatmap';


class Work extends Component {

    constructor() {
        super();
        this.state = {
            hideSidebar: false,
            onMouse: false,
            url: null,
            heatmap: false,
            lineChart: false

        }

    }


    hideSidebar = () => {
        this.setState({hideSidebar: !this.state.hideSidebar})
        if (this.state.hideSidebar) {
            $('.list').addClass('false');
            $('.hideMe').addClass('true');
        } else {
            $('.list').removeClass('false');
            $('.hideMe').removeClass('true');
        }
    }

    clickMe = () => {
        this.setState({heatmap: true, lineChart: false});
    }

    clickMe1 = () => {
        console.log('tt')
        this.setState({lineChart: true, heatmap: false});
    }


    render() {

        return (
            <div className="work">
                <div className="left">
                    <div className="list true">
                        <div className="d3examples">
                            <h2>D3 Examples</h2>
                            <ul>
                                <li onClick={this.clickMe}> HeatMap</li>
                                <li onClick={this.clickMe1}>Line Chart</li>
                            </ul>


                        </div>
                    </div>

                    <div className="hideMe false">
                        <span>
                         <i className="fa fa-bars" aria-hidden="true" onClick={this.hideSidebar}></i>
                        </span></div>
                </div>
                <div className="right">

                    {this.state.lineChart ? <LineChart/> : ''}
                    {this.state.heatmap ? <Heatmap/> : ''}
                </div>


            </div>


        )
    }


}

export default Work;



